import { useContext, useState, useEffect } from 'react';
import proyectoContext from "../../context/proyectos/proyectoContext";
import tareaContext from "../../context/tareas/tareaContext";

const FormTarea = () => {

    //Obtener el state del formulario(proyectoContext)
    const proyectosContext = useContext(proyectoContext);
    const { proyecto } = proyectosContext;

    //Obtener context de tarea
    const tareasContext = useContext(tareaContext);
    const { tareaSeleccionada, errorTarea, obtenerTareas, agregarTarea, validarTarea, 
            actualizarTarea, limpiarTarea } = tareasContext

    useEffect(() => {
        if(tareaSeleccionada !== null) {
            setTarea(tareaSeleccionada);
        } else {
            setTarea({
                nombre: ''
            });
        }
    }, [tareaSeleccionada]);

    //State del formulario
    const [ tarea, setTarea ] = useState({
        nombre: ''
    });

    const { nombre } = tarea;

    //Si no hay proyecto seleccionado
    if(!proyecto) return null;

    //Leer los valores del formulario
    const handleChange = e => {
        setTarea({
            ...tarea,
            [e.target.name]: e.target.value
        });
    }

    //Extraer el proyecto actual
    const [ proyectoActual ] = proyecto;

    const onSubmit = e => {
        e.preventDefault();

        //Validar
        if(nombre.trim() === '') {
            validarTarea();
            return;
        }

        //Revisar si es edicion o agregar
        if(tareaSeleccionada === null) {
            //agregar la nueva tarea
            tarea.proyecto = proyectoActual._id;
            agregarTarea(tarea);
        } else {
            actualizarTarea(tarea);
            limpiarTarea();
        }

        //Obtener y filtrar tareas del proyecto actual
        obtenerTareas(proyectoActual.id);

        //reiniciar el form
        setTarea({
            nombre: ''
        });
    }

    return (
        <div className="formulario">
            <form
                onSubmit={onSubmit}
            >
                <div className="contenedor-input">
                    <input 
                        type="text"
                        className="input-text"
                        placeholder="Nombre Tarea..."
                        name="nombre"
                        value={nombre}
                        onChange={handleChange}
                    />
                </div>
                <div className="contenedor-input">
                    <input 
                        type="submit"
                        className="btn btn-primario btn-submit btn-block"
                        value={tareaSeleccionada ? 'Editar Tarea' : 'Agregar Tarea'}
                    />
                </div>
            </form>

            {errorTarea ? <p className="mensaje error">El nombre de la tarea es obligatorio</p> : null}
        </div>
    )
}

export default FormTarea;
