import { useContext } from 'react'

import proyectoContext from "../../context/proyectos/proyectoContext";
import tareaContext from "../../context/tareas/tareaContext";

const Tarea = ({tarea}) => {

    //Obtener el state del formulario(proyectoContext)
    const proyectosContext = useContext(proyectoContext);
    const { proyecto } = proyectosContext;

    //Extraer el proyecto actual
    const [ proyectoActual ] = proyecto;


    //Obtener context de tarea
    const tareasContext = useContext(tareaContext);
    const { obtenerTareas, eliminarTarea, guardarTareaActual, actualizarTarea } = tareasContext

    const tareaElimininar = id => {
        eliminarTarea(id, proyectoActual._id);
        obtenerTareas(proyectoActual._id);
    }

    const cambiarEstado = tareaActual => {
        if(tareaActual.estado) {
            tareaActual.estado = false;
        } else {
            tareaActual.estado = true;
        }

        actualizarTarea(tareaActual);
    }

    const seleccionarTarea = tareaActual => {
        guardarTareaActual(tareaActual);
    }

    return (
        <li className="tarea sombra">
            <p>{tarea.nombre}</p>
            <div className="estado">
                {tarea.estado
                    ?
                        (
                            <button
                                type="button"
                                className="completo"
                                onClick={() => cambiarEstado(tarea)}
                            >
                                Completo
                            </button>
                        )
                    :
                        (
                            <button
                                type="button"
                                className="incompleto"
                                onClick={() => cambiarEstado(tarea)}
                            >
                                    incompleto
                            </button>
                        )
                }
            </div>
            <div className="acciones">
                <button
                    type="button"
                    className="btn btn-primario"
                    onClick={() => seleccionarTarea(tarea)}
                >
                    Editar
                </button>
                <button
                    type="button"
                    className="btn btn-secundario"
                    onClick={() => tareaElimininar(tarea._id)}
                >
                    Eliminar
                </button>
            </div>
        </li>
    )
}

export default Tarea
